body {
    background-color: #272323;
}
   
h1{
    color: rgb(255, 217, 0);
    text-align: center;
    font-family: 'IBM Plex Sans Thai Looped', sans-serif;
    text-shadow: 1px 5px 2px rgba(0, 0, 0, 0.9);
}
.engin{
    text-align: center;
}
.inputbox {
    font-weight: bold;
    height: 100px;
    width: 800px;
    margin: 15px;
    border-radius: 5rem;
    border-style: solid;
    border-color: #eeeeee;
    box-shadow: 10px 10px 36px -10px rgba(0,0,0,0.75);
    padding: 15px;
    font-size: large;
    font-family: 'IBM Plex Sans Thai Looped', sans-serif;

}

.transbtn {
    font-weight: bold;
    padding: 15px;
    background-color: #4CAF50;
    color: white;
    font-size: medium;
    border-style: none;
    border-radius: 15rem;
    box-shadow: 10px 10px 36px -10px rgba(0,0,0,0.75);
    outline:none;
    font-family: 'IBM Plex Sans Thai Looped', sans-serif;

}
.transbtn:active{
    background-color: #a5ee93;
}


.wordbox{
    text-align: center;
}

.word {
    font-weight: bold;
    margin-top: 3px;
    margin-left: 3px;
    display: inline-flex;
    padding: 5px 10px; /* You can adjust the padding as per your preference */
    border: 1px solid #ccc;
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
    font-family: 'IBM Plex Sans Thai Looped', sans-serif;
    font-size: 16px;
    color: #000000;
    line-height: 1.6;
  }
  .fas {
    margin-left: 10px;
    margin-top: 3px;
  }